export function addForm (element) {
 const myForm =  `<div class="demo-card-wide mdl-card mdl-shadow--2dp mdl-cell mdl-cell--4-co">
 <img height="200" [src]="imageUrl">

 <div class="mdl-card__supporting-text">      
<div  [dragula]='"first-bag"' [dragulaModel]='fields' id="content">

</div>
 </div>
 <div class="mdl-card__actions mdl-card--border">      
   <a id="send" [ngStyle]="btnStyle" (click)="SendFrm()" class="sndBtn mdl-button mdl-button--colored mdl-js-button mdl-js-ripple-effect">
     Send
   </a>
 </div>
 <div class="mdl-card__menu">
   <button class="mdl-button mdl-button--icon mdl-js-button mdl-js-ripple-effect">
     <i class="material-icons">share</i>
   </button>
 </div>
</div> 
<style>.demo-card-wide.mdl-card {
    width: 400px;
  }
  .demo-card-wide > .mdl-card__title {
    color: #fff;
    height: 176px;
    /* background: url('https://getmdl.io/assets/demos/welcome_card.jpg') center / cover; */
  }
  .demo-card-wide > .mdl-card__menu {
    color: #fff;
   }
   /* .sndBtn{
    width: 100% !important;
  }  */</style>`
    const content = document.getElementById("myForm");
    content.innerHTML = (myForm);
}