export function addParagraph (element) {
    const id = element.ID;
    const paragraph = document.createElement('h6');
    paragraph.id = id;
    paragraph.textContent = "write something...";
    paragraph.setAttribute("contenteditable","true");
    const wrapper = document.createElement('div');
    // wrapper.classList.add("mdl-js-textfield");
    // wrapper.classList.add("mdl-textfield")
    wrapper.appendChild(paragraph);
    const content = document.getElementById("myPage");
    content.appendChild(wrapper);
}