import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { FormBuilderComponent } from '../form-builder/form-builder.component';

declare var componentHandler: any;

@Component({
  selector: 'app-admin-dashboard',
  templateUrl: './admin-dashboard.component.html',
  styleUrls: ['./admin-dashboard.component.css']
})
export class AdminDashboardComponent implements OnInit {
  @ViewChild('formBuilder') formBuilder: FormBuilderComponent;
  imgUrl: string = "https://getmdl.io/assets/demos/welcome_card.jpg";
  fileToUpload: File;
  customInput = function (id, name, type, required) {
    this.ID = id;
    this.name = name;
    this.type = type;
    this.required = required;
  }
  type;
  name;
  dropdown: string;
  isDropdown: boolean;
  required: boolean;
  dropdownFields = [
  ];
  @ViewChild("myType", { read: ElementRef }) myType: ElementRef;
  @ViewChild("imageBtn", { read: ElementRef }) imageBtn: ElementRef;

  constructor(private elementRef: ElementRef) { }

  ngOnInit() {
    debugger
    this.eventFire(this.myType.nativeElement, 'click');
    document.addEventListener('keyup', (ev) => {
      this.onPressEnter(ev);
      return false;
    })
    this.formBuilder.imageUrl = this.imgUrl;
    this.myType.nativeElement.addEventListener('click', this.onClick.bind(this));
    let imageClick = document.querySelector('#upload-img-btn');
    if (imageClick !== null) {
      imageClick.addEventListener('click', () => {
        this.imageBtn.nativeElement.click();
      })
    }
  }
  eventFire(el, etype) {
    if (el.fireEvent) {
      el.fireEvent('on' + etype);
    } else {
      var evObj = document.createEvent('Events');
      evObj.initEvent(etype, true, false);
      el.dispatchEvent(evObj);
    }
  }
  handleFileInput(file: FileList) {
    this.fileToUpload = file.item(0);
    let reader = new FileReader();
    reader.onload = (event: any) => {
      this.imgUrl = event.target.result;
      this.formBuilder.imageUrl = this.imgUrl;
    }
    reader.readAsDataURL(this.fileToUpload);
  }
  onPressEnter(ev) {
    if (ev.keyCode === 13) {
      this.addMyElement();
    }
  }
  onClick(event) {
    if (event.srcElement.value === "dropdown") {
      this.isDropdown = true;
      setTimeout(() => {
        componentHandler.upgradeDom();
      }, 300);
    } else {
      this.isDropdown = false;
      this.dropdownFields = []
    }
  }
  addItemToDropdown(val) {
    if (val.trim() || '') {
      const option = {
        name: val
      }
      this.dropdownFields.push(option);
      this.dropdown = "";
    }
  }
  focusTo(query) {
    let el = document.querySelector(query);
    if (el !== null) {
      el.focus();
    }
  }
  addParagraph(type) {
    const myElm = {
      id: 0,
      type: type,
      content: "",
      name: undefined
    };
    this.formBuilder.addElement(myElm).then(res => {
    }).catch();
  }
  addMyElement() {
    if (this.name.length < 1) {
      return;
    }
    const myElm = new this.customInput(0, this.name, this.type, this.required);
    if (this.dropdownFields.length > 0)
      myElm.selects = this.dropdownFields;

    if (this.required === true) {
      myElm.name = `*${this.name}`;
    }
    this.formBuilder.addElement(myElm).then(res => {

    }).catch();
    this.name = "";
    this.dropdownFields = []

  }

}
